<template>
  <div>
    <b-card
      ref="cardAction"
      title="انشاء قاعة افراح"
    >
      <form-wizard
        color="#7367F0"
        :title="null"
        :subtitle="null"
        shape="square"
        finish-button-text="ارسال"
        next-button-text="التالي"
        back-button-text="السابق"
        class="mb-3"
        @on-complete="formSubmitted"
      >

        <!-- accoint details tab -->
        <tab-content
          title="تفاصيل القاعة"
          :before-change="validationForm"
        >
          <validation-observer
            ref="accountRules"
            tag="form"
          >
            <b-row>
              <b-col
                cols="12"
                class="mb-2"
              >
                <h5 class="mb-0">
                  بيانات صاحب القاعة
                </h5>
                <small class="text-muted">
                  من فضلك قم بادخال بيانات القاعة.
                </small>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="الاسم"
                  label-for="name"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="الاسم"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="name"
                      :state="errors.length > 0 ? false:null"
                      placeholder="اكتب الاسم"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="البريد الالكتروني"
                  label-for="email"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="email"
                    rules="required|email"
                  >
                    <b-form-input
                      id="email"
                      v-model="emailValue"
                      type="email"
                      :state="errors.length > 0 ? false:null"
                      placeholder="البريد الالكتروني"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="الهاتف"
                  label-for="phone"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="phone"
                    rules="required"
                  >
                    <b-form-input
                      id="phone"
                      v-model="phone"
                      :state="errors.length > 0 ? false:null"
                      placeholder="رقم الهاتف"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="كلمة المرور"
                  label-for="password"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="Password"
                    vid="Password"
                    rules="required"
                  >
                    <b-form-input
                      id="password"
                      v-model="PasswordValue"
                      type="password"
                      :state="errors.length > 0 ? false:null"
                      placeholder="كلمة المرور"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="تاكيد كلمة المرور"
                  label-for="c-password"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="Password Confirm"
                    rules="required|confirmed:Password"
                  >
                    <b-form-input
                      id="c-password"
                      v-model="passwordCon"
                      type="password"
                      :state="errors.length > 0 ? false:null"
                      placeholder="تاكيد كلمة المرور"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
            </b-row>
          </validation-observer>
        </tab-content>

        <!-- personal details tab -->
        <tab-content
          title="بيانات القاعة"
          :before-change="validationFormInfo"
        >
          <validation-observer
            ref="infoRules"
            tag="form"
          >
            <b-row>
              <b-col
                cols="12"
                class="mb-2"
              >
                <h5 class="mb-0">
                  بيانات القاعة
                </h5>
                <small class="text-muted">استكمال البيانات وكتابة بيانات القاعة.</small>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="الموقع االالكتروني"
                  label-for="website"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="website"
                    rules="required"
                  >
                    <b-form-input
                      id="website"
                      v-model="website"
                      placeholder="الموقع الالكتروني"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group
                  label="اسم المحافظة"
                  label-for="country"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="country"
                    rules="required"
                  >
                    <b-form-select
                      id="country"
                      v-model="country"
                      placeholder="اختار المحافظة"
                      value-field="id"
                      text-field="name"
                      :options="countryOptions"
                      :state="errors.length > 0 ? false:null"
                      @change="changeGov"
                    />

                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group
                  label="اسم المدينة"
                  label-for="city"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="city"
                    rules="required"
                  >
                    <b-form-select
                      id="city"
                      v-model="city"
                      placeholder="اختار المدينة"
                      value-field="id"
                      text-field="name"
                      :options="cityOptions"
                      :state="errors.length > 0 ? false:null"
                    />

                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="رقم الهاتف المباشر"
                  label-for="landline_number"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="landline_number"
                    rules="required"
                  >
                    <b-form-input
                      id="landline_number"
                      v-model="landline_number"
                      :state="errors.length > 0 ? false:null"
                      placeholder="رقم الهاتف المباشر"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="اسم الموظف المباشر"
                  label-for="direct_employee_name"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="direct_employee_name"
                    rules="required"
                  >
                    <b-form-input
                      id="direct_employee_name"
                      v-model="direct_employee_name"
                      placeholder="اسم الموظف المباشر"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group
                  label="رقم هاتف الموظف المباشر"
                  label-for="direct_employee_phone"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="direct_employee_phone"
                    rules="required"
                  >
                    <b-form-input
                      id="direct_employee_phone"
                      v-model="direct_employee_phone"
                      placeholder="رقم هاتف الموظف المباشر"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="سجل القاعة"
                  label-for="factory_register"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="factory_register"
                    rules="required"
                  >
                    <b-form-input
                      id="factory_register"
                      v-model="commercial_register"
                      :state="errors.length > 0 ? false:null"
                      placeholder="سجل القاعة"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="صورة سجل القاعة"
                  label-for="factory_register_image"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="factory_register_image"
                    rules="required"
                  >
                    <b-form-file
                      id="factory_register_image"
                      v-model="factory_register_image"
                      type="image"
                      :state="errors.length > 0 ? false:null"
                      placeholder="صورة سجل القاعة"
                      @change="fileFactory"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="صورة سجل القاعة"
                  label-for="letter_image"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="letter_image"
                    rules="required"
                  >
                    <b-form-file
                      id="letter_image"
                      v-model="letter_image"
                      :state="errors.length > 0 ? false:null"
                      placeholder="صورة خطاب القاعة"
                      @change="fileLetter"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group
                  label="العنوان"
                  label-for="address"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="address"
                    rules="required"
                  >
                    <b-form-input
                      id="address"
                      v-model="address"
                      :state="errors.length > 0 ? false:null"
                      placeholder="العنوان"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
            </b-row>
          </validation-observer>
        </tab-content>
      </form-wizard>
    </b-card>
  </div>
</template>

<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import {
  BFormSelect,
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BCard,
  BFormFile,
} from 'bootstrap-vue'
import { required, email } from '@validations'
// import ar from 'vee-validate/dist/locale/ar.json'

// Install English and Arabic locales.
// localize({
//   dic,
// })
const dic = {
  ar: {
    names: {
      email: 'البريد الاليكتروني',
      phone: 'رقم الهاتف',
      Password: 'كلمة السر',
      'Password Confirm': 'تاكيد كلمة المرور',
      website: 'الموقع الالكتروني',
      country: 'المحافظة',
      landline_number: 'الرقم المباشر',
      direct_employee_name: 'اسم الموظف المباشر',
      direct_employee_phone: 'رقم هاتف الموظف المباشر',
      commercial_register: 'سجل القاعة',
      factory_register_image: 'صورة سجل القاعة',
      letter_image: 'صورة الخطاب',
      address: 'العنوان',
    },
  },
}
// localize('ar')
localize('ar', dic.ar)
export default {
  name: 'CreateWedding',
  components: {
    ValidationProvider,
    ValidationObserver,
    FormWizard,
    BCard,
    BFormFile,
    TabContent,
    BRow,
    BCol,
    BFormGroup,
    BFormInput,
    BFormSelect,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
  },
  data() {
    return {
      factory_file: null,
      letter_file: null,
      countryOptions: [],
      cityOptions: [],
      name: '',
      emailValue: '',
      phone: '',
      country: '',
      city: '',
      PasswordValue: '',
      passwordCon: '',
      website: '',
      landline_number: '',
      direct_employee_name: '',
      direct_employee_phone: '',
      commercial_register: '',
      factory_register_image: null,
      letter_image: null,
      address: '',
      required,
      email,
      // ? Default locale for VeeValidate is 'en'
      locale: 'ar',
    }
  },
  created() {
    this.$store.dispatch('common/FETCH_GOV').then(res => {
      this.countryOptions = res.data.data.governorates
    })
  },
  methods: {
    fileFactory(event) {
      // eslint-disable-next-line prefer-destructuring
      this.factory_file = event.target.files[0]
    },
    fileLetter(event) {
      // eslint-disable-next-line prefer-destructuring
      this.letter_file = event.target.files[0]
    },
    changeGov(event) {
      this.$store.dispatch('common/FETCH_CITY', event).then(res => {
        this.cityOptions = res.data.data.cities
      })
    },
    formSubmitted() {
      const formData = new FormData()
      formData.append('name', this.name)
      formData.append('email', this.emailValue)
      formData.append('phone', this.phone)
      formData.append('country_id', this.country)
      formData.append('category_id', 3)
      formData.append('city_id', this.city)
      formData.append('password', this.PasswordValue)
      formData.append('lat', '0.745')
      formData.append('lng', '0.54')
      formData.append('website', this.website)
      formData.append('landline_number', this.landline_number)
      formData.append('direct_employee_name', this.direct_employee_name)
      formData.append('commercial_register', this.commercial_register)
      formData.append('commercial_register_image', this.factory_file)
      //  formData.append('factory_register_image', this.factory_file)
      formData.append('letter_image', this.letter_file)
      formData.append('address', this.address)
      formData.append('direct_employee_number', '0123456789')

      this.$store.dispatch('company/store', formData).then(() => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'تم إضافة القاعة بنجاح',
            icon: 'EditIcon',
            variant: 'success',
          },
        })
      })
    },
    validationForm() {
      return new Promise((resolve, reject) => {
        this.$refs.accountRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormInfo() {
      return new Promise((resolve, reject) => {
        this.$refs.infoRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
  },
}
</script>
  <style scoped>

  </style>
